@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: #121212;
  height:100vh;
  width:100vw;
  overflow-x:hidden;
}
.App{
  margin:0 0 ;
  color:white;
  font-family: inherit;
  font-weight: 350;
  line-height: 1.1;
  text-align:center;
  letter-spacing:1px ;
  height:100vh;
}



.ri-cloud-windy-line{
  font-size:40px ;
}
.header{
  display:inline-block;
 margin-top: 2%;
  font-size:60px ;
}
.header2{
 
  display:inline-block;
  margin-top: 2%;
  font-size:40px ;
  font-family:'Merriweather Sans', sans-serif;
}



@media only screen and (max-width: 768px){

  .header2{
    font-size:35px;
  }
  .header{
    font-size:50px ;
  }

}

.x{
    color:white;
    border-radius: 2em;
    width: 300px;
    height: 30%;
    margin:3em auto;
    padding:1em;
}
.x>a{
    color:blue;
    transition: all 0.5s ease-in;
}
.x>a:hover{
    color:coral

}

.re{
    margin-top:2%
}

.x>i{
    font-size:50px;

}

.qq{
    margin-top:2em;
}

.ll{
    margin:0 10px;

}

.changeunit{
    background:none;
    outline:none;
    border:none;
    color:white;
}
.changeunit>i{
    font-size: 1em;
}



.detail{
    text-align:left;
    margin:1em auto;
    width: 50%;
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    color:#121212;
    padding:1em;
    border-radius:1em;
  
}
.detail>a{
    font-size:24px;
}
.detail>a>i{
    font-size:24px;

}


.detail-head{
    margin-top: 2%;
}

.moda{
    background-color:white;
    z-index: 9999;
}

@media only screen and (max-width: 768px){
    .detail>a{
        margin-top: 20%;

    }
    
}
footer{
    position:Fixed;
    bottom:0;
    width:100%;
    padding:2em;
    text-align:center;
    background-color:#212121;
}

i{
    font-size:20px;
}
.dd{
    margin:3em 20px;
}


a{
    text-decoration:none;
    text-align: center;
    color:white;
    margin:0 20px;
    transition: all 0.3s ease-in;
    font-size:20px;
}
.footer-header{
    font-size:20px;
    color:white;
    text-align:center;
   
}
@media only screen and (max-width: 768px){
    .dd{
        display:flex;
      margin:28px auto;
      flex-wrap: wrap;
     

    }
   .dd>a{
        margin-top:15%;
        font-size:20px;
        
    }


    footer{
        padding:0.2em;
        position: static;
        
    }
    .footer-header{
        font-size:17px;
    }
}

.burgur{
    color:white;
    font-size:25px

}
.navbar-brand{
    font-size:25px;
}

.head-b{
    font-size:18px
}
.bg{
    background-color: aliceblue;
}


.nav-link{

    transition: all 0.2s ease-in;
   
}

.nav-link:hover{

    background-color:white;
    
}



@media only screen and (max-width: 768px)
{
    .nav{
        padding:0 0.5em;
        display:flex;
        justify-content:center;
        
    }
    .nav-link{
        font-size:15px;
    }
}

